import {JSXElementConstructor, ReactElement, useEffect, useState} from "react";
import {base_URL} from "../notifications/constants";
import {useAppSelector} from "../../app/hooks";
import {selectDarkMode, selectUserKey} from "../../app/appDataSlice";
import {Autocomplete, AutocompleteItem, Card, Modal} from "@ui-kitten/components";
import {Alert, Button, View, Text} from "react-native";
import {useGetUserInfoQuery} from "../api/usersApi";
import {useGetCurrentSectionQuery} from "../api/classPagesApi";
import renderItem from "./ScheduledItem";

export default function renderModal(setShowModal, showModal) {
    const {data: userData} = useGetUserInfoQuery(useAppSelector(selectUserKey));
    const {data: currentSection} = useGetCurrentSectionQuery();
    const darkMode = useAppSelector(selectDarkMode)
    const userKey = useAppSelector(selectUserKey)
    const [ names, setNames ] = useState([])
    const [ users, setUsers ] = useState([])
    const [ value, setValue ] = useState('')
    const [ hasChosen, setHasChosen ] = useState(false)
    const [ recipient, setRecipient ] = useState(null)

    const myStyles = {
        modal: {
            fontSize: 18, paddingBottom: 20, fontWeight: 'bold',
            textAlign: 'center', width: '100%', color: darkMode ? 'rgb(255,255,255)' : 'rgb(0,0,0)'
        },
        backdrop: {
            backgroundColor: darkMode ? 'rgba(141,141,141,0.5)' : 'rgba(0, 0, 0, 0.5)',
        },
        containerDrop: {
            minWidth: 200,
            paddingBottom: 15
        },
    }

    useEffect(() => {
        fetch(base_URL + '/users/userNamesAndNames', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'userKey': userKey
            }
        }).then(async (data) => {
            if (data.ok) {
                const userList = await data.json();
                setNames(userList.map(user => {
                    if (user) {
                        return user.realName + ' (' + user.userName + ')'
                    }
                }))
                setUsers(userList)
            }
        })
    }, [])
    const renderDropDownForExport = () => {

        function renderEachDropItem() {
            let final = []
            for (const option in names) {
                final.push(
                    <AutocompleteItem
                        title={names[option]}
                        key={option}
                    />
                )
            }
            return final
        }

        const onChangeText = (query) => {
            setNames(
                freshNames().filter(name =>
                    name?.toLowerCase().includes(query.toLowerCase())
                    || userNameContainsString(name, query))
            );
            setValue(query);
        };

        function freshNames() {
            return users.map(user => {
                if (user) {
                    return user.realName + ' (' + user.userName + ')'
                }
            })
        }

        const userNameContainsString = (name, query) => {
            const user = users.find(auser => auser?.userName.toLowerCase().includes(query.toLowerCase()) && auser?.realName === name);
            return user !== undefined;
        }

        function handleSelection(index) {
            if (index === 0) {
                setHasChosen(false)
                setRecipient(null)
                // @ts-ignore
                setValue('')
            } else {
                setHasChosen(true)
                setValue(names[index - 1])
                setRecipient(findUserNameFromName(names[index - 1]))
            }
        }

        function findUserNameFromName(name) {
            const firstPar = name.indexOf('(')
            const secondPar = name.indexOf(')')
            return name.slice(firstPar + 1, secondPar)
        }

        return (
            <Autocomplete
                style={myStyles.containerDrop}
                value={value}
                onChangeText={onChangeText}
                onSelect={handleSelection}>
                <AutocompleteItem title={' '}/>
                {renderEachDropItem()}
            </Autocomplete>
        );
    };

    const exportSchedule = async (recipient) => {
        function convert(addedClasses) {
            let final = []
            Object.keys(addedClasses).map((term) => {
                if (term === currentSection) {
                    Object.keys(addedClasses[term]).map((masterCourseCode) => {
                        for (const courseCode of addedClasses[term][masterCourseCode]) {
                            final.push({
                                courseCode,
                                courseTerm: currentSection
                            })
                        }
                    });
                }
            });
            return final
        }
        let status = await fetch(base_URL + '/users/sendNotification', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'userKey': userKey
            },
            body: JSON.stringify({
                recipients: [recipient],
                text: JSON.stringify(convert(userData.addedClasses?.classes)),
                title: userData.realFirstName + "'s Schedule",
                isSchedule: true
            })
        })
        return status.ok
    }
    return (
        <Modal
            visible={showModal}
            backdropStyle={myStyles.backdrop}
            style={{ width: '80%', height: '70%' }}
            onBackdropPress={() => setShowModal(false)}>
            <Card disabled={true}>
                <Text style={myStyles.modal}>
                    Share Your Schedule
                </Text>
                {renderDropDownForExport()}
                <View>
                    <Button disabled={recipient === null} title={'Send'} onPress={() => {
                        exportSchedule(recipient).then((returnValue) => {
                            setShowModal(false)
                            returnValue ? Alert.alert('Message Sent') :
                                Alert.alert('Error Sending Message', 'Please Try Again Later')
                        })
                    }} />
                    <Button title={'Dismiss'} onPress={() => {
                        setShowModal(false)
                    }} />
                </View>
            </Card>
        </Modal>
    );
}