import { View, TouchableOpacity, Alert } from 'react-native';
import { Card, Layout, Text, useTheme } from '@ui-kitten/components';
import { useState, useContext, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-native-fontawesome';
import {
  faHeart as heartSolid,
  faHeart,
} from '@fortawesome/free-solid-svg-icons';
import { stringToEmoji } from '../../../functions';
import { faHeart as heartOutLine } from '@fortawesome/free-regular-svg-icons';
import { useAppSelector } from '../../../app/hooks';
import { selectDarkMode, selectUserKey } from '../../../app/appDataSlice';
import {
  useDeleteCommentMutation,
  useLikeCommentMutation,
} from '../../api/discoverApi';

export default function Comment({
  body,
  postId,
  commentId,
  isOwnComment,
  userName,
  numLikes,
  hasLiked,
  searchTerm,
  searchedTag,
}) {
  const theme = useTheme();
  const userKey = useAppSelector(selectUserKey);
  const darkMode = useAppSelector(selectDarkMode);
  const [isExpanded, setIsExpanded] = useState(true);
  const [likeComment] = useLikeCommentMutation();
  const [deleteComment, deleteCommentLoading] = useDeleteCommentMutation();

  const handleLikeComment = async () => {
    try {
      await likeComment({
        commentId,
        postId,
        userKey,
        userName,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.warn("Couldn't like comment", error);
    }
  };

  const handleDeleteComment = () => {
    try {
      deleteComment({
        commentId,
        postId,
        userKey,
        userName,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.warn("Couldn't delete comment", error);
    }
  };

  return (
      <TouchableOpacity
        style={{ flexDirection: 'row', marginVertical: 10, marginHorizontal: 15 }}
        onPress={() => setIsExpanded(!isExpanded)}
        onLongPress={() => {
          if (isOwnComment) {
            Alert.alert('', body, [
              {
                text: 'Cancel',
                style: 'cancel',
              },
              {
                text: 'Delete',
                onPress: handleDeleteComment,
              },
            ]);
          }
        }}>
        <Layout
          style={{
            height: '100%',
            flexDirection: 'row',
            flex: 11,
          }}>
          <Layout
            style={{
              borderRadius: 100,
              width: 35,
              height: 35,
              marginRight: 10,
              backgroundColor: darkMode
                ? theme['color-basic-600']
                : theme['color-basic-300'],
              justifyContent: 'center',
              alignItems: 'center',
            }}>
            <Text style={{ fontSize: 15 }}>{stringToEmoji(body)}</Text>
          </Layout>

          <Text style={{ fontWeight: 'normal', flex: 1, paddingTop: 7 }}>
            {`  `}
            {isExpanded || body.length <= 50 ? body : `${body.slice(0, 50)}...`}
          </Text>
        </Layout>

        <TouchableOpacity
          style={{
            flexDirection: 'row',
            alignSelf: 'center',
            alignItems: 'center',
          }}
          onPress={handleLikeComment}>
          <Text style={{ padding: 5, fontSize: 15 }}>{numLikes}</Text>
          <FontAwesomeIcon
            size={15}
            color={
              hasLiked ? theme['color-primary-500'] : theme['color-basic-400']
            }
            icon={heartSolid}
          />
        </TouchableOpacity>
      </TouchableOpacity>
  );
}
