import { A } from '@expo/html-elements';
import { Input, Layout, Text } from '@ui-kitten/components';
import { Image, Dimensions } from 'react-native';
import { selectDarkMode } from '../../../app/appDataSlice';
import { useAppSelector } from '../../../app/hooks';
import DoubleTap from '../../../components/DoubleTap';
import { MAX_WIDTH_WEB } from '../../../constants/discover';
import { getImageUrl } from '../../../functions';
import { validURL } from '../../../functions';

const windowWidth = Dimensions.get('window').width;

export default function SinglePostBody({ type, body, handleLikePost }) {
  const darkMode = useAppSelector(selectDarkMode);

  if (type === 'contentText') {
    return (
      <Input
        scrollEnabled={false}
        multiline
        disabled
        value={body}
        style={{
          backgroundColor: 'transparent',
          borderWidth: 0,
        }}
        textStyle={{ color: darkMode ? 'white' : 'black' }}
      />
    );
  } else if (type === 'contentUrl') {
    if (validURL(body)) {
      return (
        <A style={{ color: 'blue' }} href={body}>
          {body}
        </A>
      );
    } else {
      return <Text>{body}</Text>;
    }
  } else {
    // Image Post
    return (
      <DoubleTap onDoubleTap={handleLikePost}>
        <Layout
          style={{
            alignItems: 'center',
          }}>
          <Image
            source={{
              uri: getImageUrl(body),
            }}
            style={{
              borderRadius: 5,
              width: windowWidth * 0.9,
              height: windowWidth * 0.9,
              maxHeight: MAX_WIDTH_WEB,
              maxWidth: MAX_WIDTH_WEB,
              marginBottom: 10,
              resizeMode: 'stretch',
            }}
          />
        </Layout>
      </DoubleTap>
    );
  }
}
