import {Alert, Text, TouchableOpacity, View} from "react-native";
import React from "react";
import CalendarFunctions from "./CalendarFunctions";
import {useAppSelector} from "../../app/hooks";
import {useRemoveClassMutation} from "../api/classPagesApi";

export default function renderCustomEvent(event, userData) {
    const calendarFun = new CalendarFunctions()

    const [startHour, startMinute] = event.startTime.split(':');
    const [endHour, endMinute] = event.endTime.split(':');
    let color = calendarFun.stringToColour(event.name);


    return (
        <View
            key={event.name + event.days}
            style={{
                backgroundColor: color,
                top: (41.7 * startHour) + (startMinute * 0.7),
                height:
                    41.7 * (endHour - startHour) + calendarFun.getMinutesDiff(startMinute, endMinute),
                width: '100%',
                position: 'absolute',
                margin: 0,
                justifyContent: 'center',
            }}>
            <TouchableOpacity
                onLongPress={() => {}
                    /* Alert.alert(
                        event.name,
                        calendarFun.convertTo12HourTime(event.startTime) +
                        ' - ' +
                        calendarFun.convertTo12HourTime(event.endTime),
                        [
                            {
                                text: 'Remove',
                                onPress: () => {
                                    //removeClass(event);
                                },
                            },
                            {
                                text: 'Back',
                                onPress: () => {},
                                style: 'cancel',
                            },
                        ],
                        { cancelable: true }
                    )*/
                }>
                <Text
                    adjustsFontSizeToFit={true}
                    numberOfLines={endHour - startHour + 2}
                    style={{ color: 'white', textAlign: 'center' }}>
                    {event.name}
                </Text>
            </TouchableOpacity>
        </View>
    );
};