import {
  StyleSheet,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Keyboard,
  ScrollView,
  KeyboardAvoidingView,
  Platform,
} from 'react-native';
import React, { useState } from 'react';
import {
  Text,
  Input,
  Select,
  SelectItem,
  IndexPath,
  Layout,
  Button,
  Spinner,
  useTheme,
  CheckBox,
} from '@ui-kitten/components';
import { FontAwesome5 } from '@expo/vector-icons';

import CreateImage from './CreateImagePost';
import {
  tagIndices,
  adminTagIndices,
  postOptions,
} from '../../../constants/discover';
import { useCreateDiscoverPostMutation } from '../../api/discoverApi';
import { useAppSelector } from '../../../app/hooks';
import { usersApiSlice } from '../../api/usersApi';
import Toast from 'react-native-root-toast';
import { selectDarkMode } from '../../../app/appDataSlice';

export default function CreatePost() {
  const theme = useTheme();
  const darkMode = useAppSelector(selectDarkMode);
  const userKey = useAppSelector((state) => state.appData.secure.userKey);
  const {
    data: { userName, permissionLevel },
  } = usersApiSlice.endpoints.getUserInfo.useQueryState(userKey);
  const [discoverPostInputs, setDiscoverPostInputs] = useState(null);
  const [selectedTagIndex, setSelectedTagIndex] = useState<IndexPath[]>([]);
  const [selectedPostTypeIndex, setSelectedPostTypeIndex] = useState(
    new IndexPath(0)
  );
  const [error, setError] = useState(null);
  const [isEmojiShown, setIsEmojiShown] = useState(false);
  const [isPostPinned, setIsPostPinned] = useState(false);

  const [createDiscoverPost, { isLoading }] = useCreateDiscoverPostMutation();

  const tagArray = permissionLevel === 'admin' ? adminTagIndices : tagIndices;

  const submitImage = async (tags: string[]) => {
    var formdata = new FormData();
    if (isPostPinned) {
      formdata.append('pinned', 'true');
    }
    formdata.append('title', discoverPostInputs.title);
    formdata.append('type', 'contentImage');
    formdata.append('picture', {
      uri: discoverPostInputs.contentImage.uri,
      name: 'discoverImage',
      type: 'image/jpg',
    } as any);
    tags.forEach((tag) => {
      formdata.append('tags', tag);
    });

    try {
      await createDiscoverPost({
        type: 'contentImage',
        userKey,
        userName,
        imagePostBody: formdata,
      }).unwrap();

      let toast = Toast.show('Posted Successfully!', {
        duration: Toast.durations.LONG,
        opacity: 1,
        position: -100,
        backgroundColor: theme['color-success-500'],
        shadow: false,
      });

      setTimeout(function hideToast() {
        Toast.hide(toast);
      }, 7000);

      setDiscoverPostInputs(null);
      setSelectedTagIndex([]);
      setError(null);
    } catch (e) {
      let toast = Toast.show('Post failed to send.', {
        duration: Toast.durations.LONG,
        opacity: 1,
        position: -100,
        backgroundColor: theme['color-warning-500'],
        shadow: false,
      });

      setTimeout(function hideToast() {
        Toast.hide(toast);
      }, 4000);
    }
  };

  const onSubmit = async () => {
    //
    // Validate Input
    //
    if (
      (selectedPostTypeIndex.row === 1 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.contentText)) ||
      (selectedPostTypeIndex.row === 2 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.contentUrl)) ||
      (selectedPostTypeIndex.row === 0 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.contentImage))
    ) {
      console.log('onsubmit error', discoverPostInputs);
      setError('Please Fill in All Fields');
      return;
    }
    // else if (
    //   (selectedPostTypeIndex.row === 0 || selectedPostTypeIndex.row == 1) &&
    //   !discoverPostInputs.emoji
    // )
    // {
    //   setError('Please select an emoji');
    //   return;
    // }

    //
    // Submit Post
    //

    const tags = selectedTagIndex.map((index) => tagArray[index.row]);

    // Special Case for Image Post
    if (selectedPostTypeIndex.row === 0) {
      submitImage(tags);
      return;
    }

    let type = '';
    if (selectedPostTypeIndex.row === 1) {
      type = 'contentText';
    } else if (selectedPostTypeIndex.row === 2) {
      type = 'contentUrl';
    }
    try {
      await createDiscoverPost({
        type,
        userKey,
        userName,
        tags,
        content: discoverPostInputs[type],
        title: discoverPostInputs.title,
        emoji: discoverPostInputs.emoji,
        pinned: isPostPinned,
      }).unwrap();

      let toast = Toast.show('Posted Successfully!', {
        duration: Toast.durations.LONG,
        opacity: 1,
        position: -100,
        backgroundColor: theme['color-success-500'],
        shadow: false,
      });

      setTimeout(function hideToast() {
        Toast.hide(toast);
      }, 7000);

      setDiscoverPostInputs(null);
      setSelectedTagIndex([]);
      setError(null);
    } catch (e) {
      let toast = Toast.show('Post failed to send.', {
        duration: Toast.durations.LONG,
        opacity: 1,
        position: -100,
        backgroundColor: theme['color-warning-500'],
        shadow: false,
      });

      setTimeout(function hideToast() {
        Toast.hide(toast);
      }, 4000);
    }
  };

  const PinnedCheckBox = () => {
    return (
      <CheckBox
        style={{ marginVertical: 20 }}
        checked={isPostPinned}
        onChange={(nextChecked) => setIsPostPinned(nextChecked)}>
        Pinned
      </CheckBox>
    );
  };

  const TagInput = () => {
    const groupedData = selectedTagIndex.map((index) => {
      return tagArray[index.row];
    });
    return (
      <Layout>
        <Select
          multiSelect
          value={groupedData.join(', ')}
          placeholder="Select Tags"
          selectedIndex={selectedTagIndex}
          onSelect={(index: IndexPath[]) => {
            setSelectedTagIndex(index);
          }}
          style={styles.input}>
          {tagArray.map((title, i) => (
            <SelectItem title={title} key={i} />
          ))}
        </Select>
        <Text
          style={{
            alignSelf: 'flex-end',
            marginRight: 5,
            color: 'grey',
            fontSize: 11,
          }}>
          Optional
        </Text>
      </Layout>
    );
  };

  const EmojiToggle = () => {
    return (
      <TouchableOpacity
        onPress={() => {
          setIsEmojiShown(!isEmojiShown);
          Keyboard.dismiss();
        }}
        style={{
          flex: 1,
        }}>
        <Layout
          level="3"
          style={{
            justifyContent: 'center',
            alignItems: 'center',

            borderRadius: 6,
            paddingVertical: 8,
            marginRight: 5,
          }}>
          <Text style={{ fontSize: 20 }}>
            {discoverPostInputs?.emoji || ``}
          </Text>
        </Layout>
      </TouchableOpacity>
    );
  };

  const renderPostOptions = () => {
    // Text
    if (selectedPostTypeIndex.row === 1) {
      return (
        <>
          <Layout
            style={{
              flexDirection: 'row',
              justifyContent: 'space-evenly',
              alignItems: 'center',
              marginTop: 15,
            }}>
            {/* <EmojiToggle /> */}

            <Input
              status="primary"
              placeholder="Post Title"
              onFocus={() => {
                setIsEmojiShown(false);
              }}
              value={discoverPostInputs?.title ? discoverPostInputs.title : ''}
              style={{ ...styles.input, flex: 5 }}
              onChangeText={(newText) => {
                setDiscoverPostInputs({
                  ...discoverPostInputs,
                  title: newText,
                });
              }}
            />
          </Layout>
          {TagInput()}
          <Input
            multiline
            placeholder="Post Body"
            onFocus={() => {
              setIsEmojiShown(false);
            }}
            textStyle={{ minHeight: 100 }}
            style={{ ...styles.input, alignSelf: 'center', width: "100%" }}
            value={
              discoverPostInputs?.contentText
                ? discoverPostInputs.contentText
                : ''
            }
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                contentText: newText,
              });
            }}
          />
          {permissionLevel === 'admin' && <PinnedCheckBox />}
        </>
      );
    } else if (selectedPostTypeIndex.row === 2) {
      // LINK
      return (
        <>
          <Layout
            style={{
              flexDirection: 'row',
              justifyContent: 'space-evenly',
              alignItems: 'center',
              marginTop: 15,
            }}>
            {/* <EmojiToggle /> */}
            <Input
              status="primary"
              placeholder="Post Title"
              onFocus={() => {
                setIsEmojiShown(false);
              }}
              value={discoverPostInputs?.title ? discoverPostInputs.title : ''}
              style={{ ...styles.input, flex: 5 }}
              onChangeText={(newText) => {
                setDiscoverPostInputs({
                  ...discoverPostInputs,
                  title: newText,
                });
              }}
            />
          </Layout>

          <Input
            placeholder="Post Link"
            style={styles.input}
            onFocus={() => {
              setIsEmojiShown(false);
            }}
            value={
              discoverPostInputs?.contentUrl
                ? discoverPostInputs.contentUrl
                : ''
            }
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                contentUrl: newText,
              });
            }}
          />
          {/* {PostTypeInput} */}
          {TagInput()}
          {permissionLevel === 'admin' && <PinnedCheckBox />}
        </>
      );
    } else if (selectedPostTypeIndex.row === 0) {
      // IMAGE
      return (
        <CreateImage
          setIsEmojiShown={setIsEmojiShown}
          discoverPostInputs={discoverPostInputs}
          setDiscoverPostInputs={setDiscoverPostInputs}
          TagInput={TagInput}
          PinnedCheckBox={
            permissionLevel === 'admin' ? PinnedCheckBox : () => <></>
          }
        />
      );
    }
  };

  return (
    <Layout style={{ flex: 1 }}>
      <KeyboardAvoidingView
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
        style={{ flex: 1 }}>
        <ScrollView style={{ flex: 1 }}>
          <TouchableWithoutFeedback
            style={{ flex: 1 }}
            onPress={() => {
              Keyboard.dismiss();
            }}>
            <Layout
              style={{ flex: 1, marginBottom: 100, marginHorizontal: 16 }}>
              <Select
                label="Select Post Type"
                value={postOptions[selectedPostTypeIndex.row]}
                selectedIndex={selectedPostTypeIndex}
                onSelect={(index: IndexPath) => {
                  setSelectedPostTypeIndex(index);
                  setSelectedTagIndex([]);
                  setDiscoverPostInputs(null);
                  setError(null);
                  setIsEmojiShown(false);
                }}
                style={styles.input}>
                <SelectItem
                  title="Image Post"
                  accessoryLeft={
                    <FontAwesome5
                      name="image"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
                <SelectItem
                  title="Text Post"
                  accessoryLeft={
                    <FontAwesome5
                      name="file-alt"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
                <SelectItem
                  title="Link"
                  accessoryLeft={
                    <FontAwesome5
                      name="link"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
              </Select>
              {error && (
                <Layout>
                  <Text
                    style={{
                      textAlign: 'center',
                      color: 'red',
                      fontStyle: 'italic',
                    }}>
                    {error}
                  </Text>
                </Layout>
              )}

              {renderPostOptions()}

              {!isEmojiShown && (
                <Button
                  style={styles.input}
                  onPress={onSubmit}
                  disabled={isLoading}>
                  Submit
                </Button>
              )}

              <Layout
                style={{
                  zIndex: -1,
                  position: 'absolute',
                  width: '100%',
                  height: '100%',
                  alignItems: 'center',
                  bottom: -600,
                }}>
                {/* <Image
                  source={require('../../../assets/panda.png')}
                  style={{ height: 175, width: 120 }}
                /> */}
              </Layout>
            </Layout>
          </TouchableWithoutFeedback>
        </ScrollView>
      </KeyboardAvoidingView>
      {isLoading && (
        <Layout
          style={{
            justifyContent: 'center',
            alignItems: 'center',
            width: '100%',
            height: '100%',
            position: 'absolute',
            backgroundColor: 'black',
            flex: 1,
            opacity: 0.3,
          }}>
          <Spinner size="medium" />
        </Layout>
      )}
    </Layout>
  );
}

const styles = StyleSheet.create({
  input: {
    marginVertical: 5,
  },
});
