import {
  Button,
  Icon,
  Layout,
  List,
  ListItem,
  Spinner,
  Text,
  useTheme,
} from '@ui-kitten/components';
import { useEffect, useRef, useState } from 'react';
import { FontAwesome5, MaterialIcons } from '@expo/vector-icons';
import Modal from 'react-native-modal';

import { useAppSelector } from '../../app/hooks';
import Header from '../../components/Header';
import {
  discoverApiSlice,
  postsAdapter,
  postsSelectors,
  useLazyGetDiscoverPostsQuery,
} from '../api/discoverApi';
import { Keyboard, Platform, TouchableOpacity } from 'react-native';
import { selectDarkMode, selectUserKey } from '../../app/appDataSlice';
import TextCard from './Cards/TextCard';
import { DiscoverPost } from '../../app/types';
import ImageCard from './Cards/ImageCard';
import { Divider } from 'react-native-paper';
import { usersApiSlice } from '../api/usersApi';

export default function PostsList({
  navigation,
  tag,
  searchTerm,
  recentSearches,
  handleRemoveRecentSearch,
  handleClickRecentSearch,
}: {
  navigation: any;
  tag?: string;
  searchTerm?: string;
  recentSearches?: string[];
  handleRemoveRecentSearch?: (searchTerm: string) => void;
  handleClickRecentSearch?: (searchTerm: string) => void;
}) {
  const theme = useTheme();
  const userKey = useAppSelector(selectUserKey);
  const darkMode = useAppSelector(selectDarkMode);

  const {
    data: { userName },
  } = usersApiSlice.endpoints.getUserInfo.useQueryState(userKey);

  const pageNumber = useRef(0);
  const [fetchPosts, { isFetching, isLoading, isError }] =
    useLazyGetDiscoverPostsQuery();

  const { posts, hasMorePages } =
    discoverApiSlice.endpoints.getDiscoverPosts.useQueryState(
      { pageNumber: 1, userKey, userName, tag, searchTerm },
      {
        selectFromResult: (result) => {
          return {
            hasMorePages: result.data?.hasMorePages,
            posts: postsSelectors.selectAll(
              result.data ?? postsAdapter.getInitialState()
            ),
          };
        },
      }
    );

  const fetchFirstPage = async () => {
    pageNumber.current = 1;
    await fetchPosts({
      userKey,
      pageNumber: pageNumber.current,
      userName,
      tag,
      searchTerm,
    });
  };

  const fetchMorePosts = async () => {
    if (!hasMorePages || isFetching) return;
    pageNumber.current++;
    await fetchPosts({
      userKey,
      pageNumber: pageNumber.current,
      userName,
      tag,
      searchTerm,
    });
  };

  useEffect(() => {
    fetchFirstPage();
  }, [searchTerm, tag]);

  const PinIcon = (
    <Layout
      style={{
        flexDirection: 'row',
        marginBottom: 0,
        marginTop: 10,
        marginLeft: 10,
      }}>
      <MaterialIcons
        name="push-pin"
        size={20}
        color={theme['color-primary-500']}
      />
      <Text style={{ color: theme['color-primary-500'] }}>pinned</Text>
    </Layout>
  );

  // Render Content

  const renderItem = ({ item }: { item: DiscoverPost }) => {
    const commonProps = {
      title: item.title,
      tags: item.tags,
      _id: item._id,
      postAuthor: item.userName,
      navigation,
      key: item._id,
      searchTerm,
      searchedTag: tag,
      pinnedIcon: item.pinned ? PinIcon : null,
    };

    // console.log(item.title, item.numLikes);

    switch (item.type) {
      case 'contentText':
        return (
          <TextCard
            {...commonProps}
            body={item.contentText}
            emoji={item.emoji}
            isUrl={false}
          />
        );
      case 'contentUrl':
        return (
          <TextCard
            {...commonProps}
            body={item.contentUrl}
            emoji={item.emoji}
            isUrl={true}
          />
        );
      case 'contentImage':
        return (
          <ImageCard
            {...commonProps}
            imageId={item.contentImage}
            postAuthor={item.userName}
            userName={userName}
          />
        );

      default:
        console.log('Tried to load an unknown post type:', item?.type);
        return null;
    }
  };

  const renderRecentSearch = ({ item }: { item: string }) => {
    return (
      <ListItem
        onPress={() => {
          handleClickRecentSearch(item);
          Keyboard.dismiss();
        }}
        style={{
          paddingVertical: 10,
          marginVertical: 0,
          backgroundColor: 'transparent',
        }}
        title={item}
        accessoryLeft={() => (
          <Icon
            name="clock"
            style={{
              height: 20,
              color: theme['color-basic-400'],
              marginHorizontal: 10,
            }}
          />
        )}
        accessoryRight={(props) => (
          <Button
            appearance="ghost"
            onPress={() => handleRemoveRecentSearch(item)}
            accessoryLeft={<Icon {...props} name="x" pack="feather" />}
          />
        )}
      />
    );
  };

  const FooterSpinner = () => {
    if (isFetching) {
      return (
        <Layout style={{ alignItems: 'center', paddingVertical: 10 }}>
          <Spinner />
        </Layout>
      );
    }
  };

  let content = <></>;
  if (isLoading) {
    content = (
      <Layout style={{ marginTop: 20, alignItems: 'center' }}>
        <Spinner />
      </Layout>
    );
  } else if (isError) {
    content = (
      <Layout style={{ alignItems: 'center', marginTop: 20 }}>
        <FontAwesome5
          name="exclamation"
          size={50}
          color={theme['color-danger-500']}
        />
        <Text style={{ textAlign: 'center', fontSize: 20 }}>
          Error loading posts
        </Text>
      </Layout>
    );
  } else if (posts.length === 0) {
    content = (
      <Layout style={{ alignItems: 'center', marginTop: 20 }}>
        <FontAwesome5
          name="sad-tear"
          size={50}
          color={theme['color-danger-500']}
        />
        <Text style={{ textAlign: 'center', fontSize: 20 }}>
          No posts found
        </Text>
      </Layout>
    );
  } else if (searchTerm === '' && tag === '') {
    if (recentSearches?.length > 0) {
      content = (
        <Layout style={{ flex: 1 }}>
          <List
            keyboardShouldPersistTaps="handled"
            ListHeaderComponent={() => (
              <Text
                status="success"
                style={{ fontStyle: 'italic', marginLeft: 20 }}>
                Recent Searches
              </Text>
            )}
            data={Array.from(recentSearches)}
            renderItem={renderRecentSearch}
          />
        </Layout>
      );
    } else {
      content = (
        <Layout style={{ flex: 1 }}>
          <Layout style={{ marginTop: 30, justifyContent: 'center' }}>
            <Text style={{ textAlign: 'center', fontSize: 20 }}>
              Search for posts
            </Text>
            <Text
              style={{
                textAlign: 'center',
                fontSize: 16,
                color: theme['color-basic-500'],
              }}>
              Search by title, tag, or college
            </Text>
          </Layout>
        </Layout>
      );
    }
  } else {
    content = (
      <Layout style={{ flex: 1 }}>
        <List
          contentContainerStyle={{ width: '100%' }}
          onRefresh={fetchFirstPage}
          refreshing={isLoading}
          data={posts}
          keyExtractor={(item) => item._id}
          renderItem={renderItem}
          onEndReachedThreshold={0.5}
          onEndReached={fetchMorePosts}
          ListFooterComponent={FooterSpinner}
          showsVerticalScrollIndicator={false}
          ItemSeparatorComponent={Platform.OS === 'web' ? null : Divider}
        />
        {/* <Modal isVisible={true}>
          <Layout style={{ flex: 1 }}>
            <Text>I am the modal content!</Text>
          </Layout>
        </Modal> */}
      </Layout>
    );
  }

  return <Layout style={{ flex: 1 }}>{content}</Layout>;
}
