import React, { useEffect, useState } from 'react';
import { store } from './src/app/store';
import { Provider } from 'react-redux';
import { PersistGate } from 'redux-persist/integration/react';
import { persistStore } from 'redux-persist';
import * as eva from '@eva-design/eva';
import {
  ApplicationProvider,
  IconRegistry,
  Text,
  useTheme,
} from '@ui-kitten/components';
import {
  DarkTheme,
  DefaultTheme,
  NavigationContainer,
} from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as ExpoSplashScreen from 'expo-splash-screen';
import { Ionicons } from '@expo/vector-icons';
import { StatusBar } from 'expo-status-bar';
import { GestureHandlerRootView } from 'react-native-gesture-handler';
import { RootSiblingParent } from 'react-native-root-siblings';
import Constants from 'expo-constants';
import { osName } from 'expo-device';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import {
  Alert,
  Linking,
  Platform,
  useColorScheme,
  Dimensions,
} from 'react-native';
import { BottomSheetModalProvider } from '@gorhom/bottom-sheet';

import Auth from './src/features/authentication';
import Discover from './src/features/discover';
import Calendar from './src/features/calendar';
import ClassPages from './src/features/classPages';
import { useAppDispatch, useAppSelector } from './src/app/hooks';
import {
  deviceDarkModeSet,
  mobileSiteSet,
  selectDarkMode,
  selectUserKey,
  userKeyUpdated,
} from './src/app/appDataSlice';
import Theme from './src/theme/custom-theme';
import {
  useGetUserInfoQuery,
  useLazyGetUserInfoQuery,
  useTrackLoginMutation,
} from './src/features/api/usersApi';
import { useLazyGetCurrentSectionQuery } from './src/features/api/classPagesApi';
import { FontAwesomeIconsPack } from './src/theme/font-awesome-icons';
import { useLazyGetCurrentVersionQuery } from './src/features/api/apiSlice';
import { FeatherIconsPack } from './src/theme/feather-icons';
import AdminPanel from './src/features/adminPanel';

const windowWidth = Dimensions.get('window').width;
const persistor = persistStore(store);
const Stack = createNativeStackNavigator();
const Tab = createBottomTabNavigator();

export default function Entry() {
  return (
    <Provider store={store}>
      <PersistGate persistor={persistor}>
        <RootSiblingParent>
          <GestureHandlerRootView style={{ flex: 1 }}>
            <BottomSheetModalProvider>
              <App />
            </BottomSheetModalProvider>
          </GestureHandlerRootView>
        </RootSiblingParent>
      </PersistGate>
    </Provider>
  );
}

function App() {
  const colorScheme = useColorScheme();
  const dispatch = useAppDispatch();
  const userKey = useAppSelector(selectUserKey);
  const darkMode = useAppSelector(selectDarkMode);
  const useDeviceColorScheme = useAppSelector(
    (state) => state.appData.secure.useDeviceColorScheme
  );
  const [initialState, setInitialState] = useState();
  const { data: initUserData, isError } = useGetUserInfoQuery(userKey, {
    skip: !userKey,
  });
  const [getUserInfo, { data: userData }] = useLazyGetUserInfoQuery();
  const [trackLogin] = useTrackLoginMutation();
  const [getCurrentSection] = useLazyGetCurrentSectionQuery();
  const [getCurrentVersion] = useLazyGetCurrentVersionQuery();

  useEffect(() => {
    const prepare = async () => {
      try {
        if (userKey) {
          // TODO: Figure out why /me doesnt work with web (CORS)
          getUserInfo(userKey)
            .unwrap()
            .then(async () => {
              await trackLogin(userKey);
            });
          await getCurrentSection().unwrap();
        }

        // Check for update
        try {
          const currentVersion = Constants.manifest.version;
          const {
            currentFrontEndVersion: { currentFrontEndVersion },
          } = await getCurrentVersion().unwrap();
          if (
            Platform.OS !== 'web' &&
            currentFrontEndVersion !== currentVersion
          ) {
            Alert.alert('There is a new version available!', '', [
              {
                text:
                  Platform.OS === 'android'
                    ? 'Update on the Play Store'
                    : 'Update on the App Store',
                onPress: () =>
                  Linking.openURL(
                    Platform.OS === 'android'
                      ? 'market://details?id=com.theathenanetwork.app'
                      : 'https://apps.apple.com/us/app/athena-network/id1628317342'
                  ),
              },
              {
                text: 'Cancel',
                style: 'cancel',
              },
            ]);
          }
        } catch (e) {
          console.warn('Update check failed', e);
        }
      } catch (e) {
        dispatch(userKeyUpdated('')); // Clear userKey if login fails
        console.warn('Login Fail', e);
      } finally {
        await ExpoSplashScreen.hideAsync();
      }
    };

    prepare();
  }, []);

  useEffect(() => {
    if (useDeviceColorScheme) {
      dispatch(deviceDarkModeSet(colorScheme === 'dark'));
    }
  }, [colorScheme]);

  useEffect(() => {
    dispatch(mobileSiteSet(windowWidth < 768));
  }, [windowWidth]);

  return (
    <NavigationContainer
      documentTitle={{
        formatter: (options, route) =>
          `${options?.title ?? route?.name} - Athena Network`,
      }}
      theme={darkMode ? DarkTheme : DefaultTheme}
      initialState={initialState}>
      <ApplicationProvider
        {...eva}
        theme={{ ...(darkMode ? eva.dark : eva.light), ...Theme }}>
        <StatusBar style={darkMode ? 'light' : 'dark'} />
        <IconRegistry icons={[FontAwesomeIconsPack, FeatherIconsPack]} />

        <Stack.Navigator screenOptions={{ headerShown: false }}>
          {(userData || initUserData) && !isError ? (
            <Stack.Screen name="Tabs" component={TabsPage} />
          ) : (
            <Stack.Screen name="Auth" component={Auth} />
          )}
        </Stack.Navigator>
      </ApplicationProvider>
    </NavigationContainer>
  );
}

const TabsPage = () => {
  const theme = useTheme();
  const userKey = useAppSelector(selectUserKey);
  const { data: userData } = useGetUserInfoQuery(userKey, {
    skip: !userKey,
  });
  const [isDev] = useState(__DEV__ ? true : false);

  return (
    <Tab.Navigator
      // initialRouteName={isDev ? 'Discover' : 'ClassPages'}
      screenOptions={({ route }) => ({
        tabBarHideOnKeyboard: Platform.OS === 'android',
        tabBarIcon: ({ focused }) => {
          // Create type for icon name
          type IoniconsIconName = React.ComponentProps<typeof Ionicons>['name'];
          let iconName: IoniconsIconName = 'ios-home';

          if (route.name === 'ClassPages') {
            iconName = focused ? 'book' : 'book-outline';
          } else if (route.name === 'Discover') {
            iconName = focused ? 'compass' : 'compass-outline';
          } else if (route.name === 'Calendar') {
            iconName = focused ? 'calendar' : 'calendar-outline';
          }

          return (
            <Ionicons
              name={iconName}
              size={25}
              color={
                focused ? theme['color-primary-500'] : theme['color-basic-500']
              }
            />
          );
        },
        tabBarStyle: { paddingTop: 5 },
        tabBarItemStyle: {},
        headerShown: false,
      })}>
      <Tab.Screen
        name="ClassPages"
        component={ClassPages}
        options={{
          unmountOnBlur: true,
          tabBarLabel: () => <Text style={{ fontSize: 12 }}>Class Pages</Text>,
        }}
      />
      <Tab.Screen
        name="Calendar"
        component={Calendar}
        options={{
          unmountOnBlur: true,
          tabBarLabel: () => <Text style={{ fontSize: 12 }}>Calendar</Text>,
        }}
      />
      <Tab.Screen
        name="Discover"
        component={Discover}
        options={{
          unmountOnBlur: false,
          tabBarLabel: () => <Text style={{ fontSize: 12 }}>Discover</Text>,
        }}
      />
      {userData?.permissionLevel === 'admin' && (
        <Tab.Screen
          name="Admin"
          component={AdminPanel}
          options={{
            unmountOnBlur: false,
            tabBarLabel: () => <Text style={{ fontSize: 12 }}>Admin</Text>,
          }}
        />
      )}
    </Tab.Navigator>
  );
};
