import { Layout, Text } from '@ui-kitten/components';
import { TouchableOpacity, Image, Dimensions } from 'react-native';
import { selectUserKey } from '../../../app/appDataSlice';
import { useAppSelector } from '../../../app/hooks';
import DoubleTap from '../../../components/DoubleTap';
import { MAX_WIDTH_WEB } from '../../../constants/discover';
import { getImageUrl } from '../../../functions';
import { useLikePostMutation } from '../../api/discoverApi';
import BottomBar from '../BottomBar';

const windowWidth = Dimensions.get('window').width;

export default function ImageCard({
  title,
  _id,
  navigation,
  imageId,
  tags,
  postAuthor,
  userName,
  searchTerm,
  searchedTag,
  pinnedIcon,
}) {
  const [likePost] = useLikePostMutation();
  const userKey = useAppSelector(selectUserKey);

  const handlePressLike = async () => {
    try {
      await likePost({
        userKey,
        _id,
        title,
        postAuthor,
        userName,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.log('Error liking post:', error);
    }
  };

  return (
    <Layout style={{ flex: 1, alignItems: 'center' }}>
      {pinnedIcon}
      <TouchableOpacity
        onPress={() => {
          navigation.navigate('SinglePost', { _id, searchTerm, searchedTag });
        }}
        activeOpacity={0.9}
        style={{
          flex: 1,
          alignItems: 'center',
          paddingVertical: 10,
        }}>
        <Layout
          style={{
            width: windowWidth * 0.9,
            maxWidth: MAX_WIDTH_WEB,
            paddingVertical: 10,
          }}>
          <Text style={{ fontSize: 20 }}>{title}</Text>
        </Layout>
        <DoubleTap delay={200} onDoubleTap={handlePressLike}>
          <Image
            source={{
              uri: getImageUrl(imageId),
            }}
            style={{
              borderRadius: 5,
              width: windowWidth * 0.9,
              height: windowWidth * 0.9,
              maxHeight: MAX_WIDTH_WEB,
              maxWidth: MAX_WIDTH_WEB,
              marginBottom: 10,
              resizeMode: 'contain',
            }}
          />
        </DoubleTap>

        <BottomBar
          tags={tags}
          _id={_id}
          searchTerm={searchTerm}
          searchedTag={searchedTag}
        />
      </TouchableOpacity>
    </Layout>
  );
}
